body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ccc;
}

dl {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0;
}

main { padding: .5rem 0 }
th { white-space: nowrap; }
code { font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@media screen and (min-width: 66rem) { main { padding: .5rem 1rem; }}
@media screen and (min-width: 68rem) { main { padding: .5rem 2rem; }}
@media screen and (min-width: 70rem) { main { padding: .5rem 3rem; }}
@media screen and (min-width: 72rem) {main { padding: .5rem 4rem; }}
@media screen and (min-width: 74rem) {main { padding: .5rem 5rem; }}
@media screen and (min-width: 76rem) {main { padding: .5rem 6rem; }}
@media screen and (min-width: 78rem) {main { padding: .5rem 7rem; }}
@media screen and (min-width: 80rem) {main { padding: .5rem 8rem; }}
@media screen and (min-width: 42rem) {
  #prices { 
    min-width: 20rem;
  }

  #article-page .flex { display: -webkit-flex; display: flex; }
}
#articles-page table { min-width: 45rem;}

#dashboard table { min-width: 0;}

@media screen and (max-width: 27rem) {
  #articles th:nth-child(4), #articles td:nth-child(4), #articles th:nth-child(3),
  #articles td:nth-child(3), #ventes td:nth-child(5), #ventes th:nth-child(5) {
    display: none;
  }
}

@media screen and (min-width: 64rem) {
  #articles th:nth-child(4), #articles td:nth-child(4), #articles th:nth-child(3),
  #articles td:nth-child(3), #ventes td:nth-child(5), #ventes th:nth-child(5) {
    display: none;
  }
}

dl:nth-child(1) { color: darkblue; }
dl:nth-child(2) { color: darkgreen; }
dl:nth-child(3) { color: dodgerblue; }
dl:nth-child(5) { color: darkred; }

@media screen and (max-width: 27rem) {
  #ventes th:nth-child(2), #ventes td:nth-child(2), #ventes th:nth-child(3), #ventes td:nth-child(3),
  #ventes th:nth-child(5), #ventes td:nth-child(5), #ventes th:nth-child(6), #ventes td:nth-child(6),
  #operations th:nth-child(2), #operations td:nth-child(2), #operations th:nth-child(3),
  #operations td:nth-child(4), #operations th:nth-child(4),
  #operations td:nth-child(3), #operations th:nth-child(6), #operations td:nth-child(6) {
    display: none;
  }
}

@media screen and (min-width: 64rem) {
  #dashboard .flex { display: -webkit-flex; display: flex; }
  #dashboard #ventes { -webkit-flex: auto; flex: auto; }
  #dashboard #articles { min-width: 20rem; }
}

#operations-page table { min-width: 52rem;}



#sales-page table { min-width: 50rem;}

.cell-action {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}

.flex-center-items {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.spacer, .grow { -webkit-flex: auto; flex: auto; }

.toolbar { padding: 8px 0; }

.pad-8 { padding: 8px; }

.snap-item {
  scroll-snap-align: start;
}

.snap-container {
  height: 100vh;
  overflow-y: auto;
  scroll-padding-top: 4rem;
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}
